<template>
  <div id="userList">
    <div class="sech clearfix" :UserListData="UserListData">
      <div class="left" style="width: 50%;margin-bottom: 25px;" :updateStatusData="updateStatusData">
        <el-input class="saechIput"  v-model="account" placeholder="搜索用户名"></el-input>
        <el-button type="primary" class="saechBout"  icon="el-icon-search" v-on:click="saechFn(1)"> </el-button>
      </div>
      <div class="right" style="width: 50%;text-align: right">
        <el-button size="medium" class="bot" icon="el-icon-delete">删除</el-button>
        <!--<el-button size="medium" class="bot" icon="el-icon-document">导出excel</el-button>-->
        <!--<el-button size="medium" type="primary" class="bot check" icon="el-icon-plus">批量导入</el-button>-->
      </div>
      <div class="tableBox">
        <el-dialog title="重置密码" :visible.sync="dialogFormVisible" style="text-align: left">
          <el-form :label-position="labelPosition" label-width="100px" :model="fromData" :rules="rules" ref="fromData">
            <el-form-item label="新密码" prop="password">
              <el-input v-model="fromData.password" type="password"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="passwordTow">
              <el-input v-model="fromData.passwordTow" type="password"></el-input>
            </el-form-item>
            <el-form-item label="">
              <div style="text-align: right">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitForm('fromData')">确 定</el-button>
              </div>
            </el-form-item>
          </el-form>
        </el-dialog>
        <el-table :data="tableData2" style="width: 100%" :row-class-name="tableRowClassName" :row-style="style1"  :header-row-style="style2">
        <el-table-column prop="da" label=" " width="20">
        </el-table-column>
        <el-table-column prop="companyName" label="商家名称" >
        </el-table-column>
        <el-table-column prop="account" label="登陆账号" >
        </el-table-column>
        <el-table-column prop="creatTmie" label="创建时间" >
        </el-table-column>
        <el-table-column prop="userId" label="操作">
          <template slot-scope="scope">
            <span class="clickBot" @click="handleEdit(scope.$index, scope.row)">
              <span class="el-icon-edit-outline"></span>
              <span>重置密码</span>
            </span>
            <span class="clickBot" @click="delUserfn(scope.$index, scope.row)" style="margin-left: 20px;">
              <span class="el-icon-delete"></span>
              <span> 删除用户</span>
            </span>
          </template>
        </el-table-column>
        </el-table>
      </div>
      <div class="cleae" style="text-align: right;padding-right: 20px;padding-top: 20px;">
        <el-pagination layout="prev, pager, next" :page-count="page.count" @current-change="handleCurrentChange">
        </el-pagination>
      </div>
    </div>
    <el-dialog title="删除用户" :visible.sync="dialogVisible2" width="30%" ><span>确定删除信息用户</span><span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible2 = false">取 消</el-button>
    <el-button type="primary" @click="delFn">确 定</el-button>
  </span>
    </el-dialog>
  </div>
</template>
<style scoped>
  #userList{max-width: 1690px;padding-top: 25px;text-align: left}
  .saechIput{width: 392px;margin-right: 0px;position: relative;z-index: 10;border: none 0px}
  .saechBout{border-radius: 0px 4px 4px 0px;position: relative; left: -7px;z-index: 11;font-size: 14px;font-weight: bold;width: 60px;text-align: center}
  .saechBout:hover{background-color: #409eff;border-color: #409eff}
  .saechBout{background-color: #0e72b9; border-color: #0e72b9 }
  .bot{ width: 120px;text-align: center}
  .check{background-color: #0d71b7;margin-right: 60px;}
  .success-row {  background: #d9dddd;  }
.tableBox{width: 100%;min-height: 200px;margin-top: 25px;}
  .clickBot{cursor: pointer;}
  .clickBot:hover{color:#409eff }

</style>
<script>
  import UserImg from '../../../static/img/icon/userDeFilt.png'
  import {mapState, mapActions} from 'vuex'
  import Public from '../../vuex/public.js'
  export default {
    name: 'my-usrList',
    data () {
      // 自定义密码校验
      var indeofPassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.fromData.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        user: {
          imgUlr: UserImg, // 账号图片
          name: '小明管理员'
        },
        userSaech: null,
        page:{
          page: 1,
          pageSize: 10,
          count: 1
        },
        dialogFormVisible: null,
        dialogVisible2: false,
        tableData2: [{
          userId: 'No Data',
          account: 'No Data',
          companyName: 'No Data',
          creatTmie: 'No Data'
        }],
        style1: {height: '70px'},
        style2: {height: '70px', border: 'none 0px', background: '#d9dddd', 'margin-top': '25px'},
        account:'',
        fromData: {
          userId: null,
          password: null,
          passwordTow: null
        },
        rules: {
          password: [
            { required: true, message: '请输入新密码', trigger: 'blur' },
            { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' }
          ],
          passwordTow: [
            { required: true, message: '请确认新密码', trigger: 'blur' },
            { validator: indeofPassword, trigger: 'blur' }
          ]
        },
        labelPosition: 'left'
      }
    },
    methods: {
      ...mapActions({
        delUser: 'delUserList',
        getUserList: 'getUserList',
        resetPassword: 'resetPassword'
      }),
      tableRowClassName ({row, rowIndex}) {
        var x = rowIndex % 2
        if (x === 0) {
          return ''
        } else if (x === 1) {
          return 'success-row'
        }
        return ''
      },
      handleCurrentChange (val) {
        this.page.page = parseInt(val)
        this.saechFn()
      },
      login () {
        this.$router.push({ name: "map" })
      },
      saechFn(a){
        if(a){
          this.page.page = 1
        }
        var parm = {
          account:this.account,
          page: this.page.page,
          pageSize: this.page.pageSize,
        }
        this.getUserList(parm)
      },
      // 解析用户列表
      userListFn(res){
        if (res.body.count) {
          var cunt = res.body.count
          // 取整
          this.page.count = Math.ceil(cunt / this.page.pageSize)
        }
         res = res.body.data
        this.tableData2 = []
        var time = Public.publicLo.timeCheng()
        for (let i in res) {
           if(res[i].creatTmie){
             time = Public.publicLo.timeCheng(res[i].creatTmie)
           }
           var pu = {
             userId: res[i].userId,
             account: res[i].account,
             companyName: res[i].companyName,
             creatTmie: time.times
           }
          this.tableData2.push(pu)
        }
      },
      // 操作
      handleEdit (index, row) {
        this.dialogFormVisible = true;
        this.fromData.userId = row.userId
      },
      // delUser
      delUserfn (index, row) {
        this.dialogVisible2 = true;
        this.fromData.userId = row.userId
      },
      delFn(){
        var parm = {
          userId: this.fromData.userId
        }
        this.dialogVisible2 = false
        this.delUser(parm)
      },
      // 操作是否成功
      listStatus (res) {
        if (res.status == 0) {
          // 修改成功
          this.openSuccess(res.message);
          // 刷新列表
          var parm = {
            account:this.account,
            page: this.page.page,
            pageSize: this.page.pageSize,
          }
          this.getUserList(parm)
        }
        if (res.status != 0) {
          // 修改 修改失败
          this.openError(res.message)
        }
        // 清空 vuex 中的本次记录，以免被下次操作状态造成混淆
        this.$store.commit('updateStatusTo', {body: null})
      },
      // 重置密码
      updatePaswordFn () {
         var parm = {
           userId: this.fromData.userId,
           password: this.fromData.password
         }
         this.resetPassword(parm);
         this.dialogFormVisible = false
      },
      // 表单验证按钮
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.updatePaswordFn()
          } else {
            return false
          }
        })
      },
      openSuccess (text) {
        this.$message({
          message: text,
          type: 'success'
        })
      },
      openError (text) {
        this.$message.error(text)
      }
    },
    computed: {
      UserListData: function () {
        var parm = this.UserList
        if (parm) {
          this.userListFn(parm)
        }
        return 1
      },
      updateStatusData: function () {
        var parm = this.updateStatus
        if (parm) {
          this.listStatus(parm)
        }
        return 1
      },
      ...mapState([
        'addOderData',
        'language',
        'UserList',
        'updateStatus'
      ])
    },
    mounted () {
      // 配置顶部菜单
      var parm = {
        account: this.account,
        page: this.page.page,
        pageSize: this.page.pageSize,
      }
     this.getUserList(parm)
    },
    components: {
    }
  }
</script>




